.sns-pimg img {
    width: 100%;
}
.product-image-wrapper h4,.product-image-wrapper h3{
  margin-top:0;
}
.choose ul li a{
  text-align: left;
}
.color-available {
  list-style: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.color-available span {
  float: left;
    width: 17px;
    height: 17px;
    margin: 0 5px 5px 0;
    /* border-radius: 30px; */
    border: 1px solid #dfdfdf;
    -webkit-box-shadow: 2px 3px 3px #f3f3f3;
            box-shadow: 2px 3px 3px #f3f3f3;
}
.p-details li{
    font-family: Arial;
    font-size: 13px;
    color: #222;
    line-height: 23px;
}
.p-details li > span{
  color:#777;
}
.p-details li{width:49%; float:left}
.sns-pimg{
  width:120px;
  height: 120px;
  text-align: center;
  overflow: hidden;
}
.sns-pimg img {
  width:100%;
  height: auto;
}


