@-webkit-keyframes listen {
    0% {
        -webkit-transform: scale(.5);
    }
    25% {
        -webkit-transform: scale(.25);
    }
    35% {
        -webkit-transform: scale(.10);
    }
    65% {
        -webkit-transform: scale(.55);
    }
    80% {
        -webkit-transform: scale(.5);
    }
    100% {
        -webkit-transform: scale(.25);
    }
}

@keyframes listen {
    0% {
        -webkit-transform: scale(.5);
                transform: scale(.5);
    }
    25% {
        -webkit-transform: scale(.25);
                transform: scale(.25);
    }
    35% {
        -webkit-transform: scale(.10);
                transform: scale(.10);
    }
    65% {
        -webkit-transform: scale(.55);
                transform: scale(.55);
    }
    80% {
        -webkit-transform: scale(.5);
                transform: scale(.5);
    }
    100% {
        -webkit-transform: scale(.25);
                transform: scale(.25);
    }
}

.mic {
    position: fixed;
    top: -430px !important;
    right: -210px;
    margin: 0px;
    top: 0px;
    -webkit-transform: scale(.2);
    -ms-transform: scale(.2);
    transform: scale(.2);
    z-index: 11;
    shape-rendering: auto;
}

.shadow {
    -moz-filter: drop-shadow( 8px 8px 8px #888888);
    -webkit-filter: drop-shadow( 8px 8px 8px #888888);
    -ms-filter: drop-shadow( 8px 8px 8px #888888);
    -o-filter: drop-shadow( 8px 8px 8px #888888);
    filter: drop-shadow( 8px 8px 8px #888888);
}

.grey-circle {
    position: fixed;
    top: -440px;
    right: -200px;
    pointer-events: none;
    -webkit-animation-name: listen;
    -webkit-animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-name: listen;
    -moz-animation-duration: 2s;
    -moz-animation-iteration-count: infinite;
    -ms-animation-name: listen;
    -ms-animation-duration: 2s;
    -ms-animation-iteration-count: infinite;
    -o-animation-name: listen;
    -o-animation-duration: 2s;
    -o-animation-iteration-count: infinite;
    animation-name: listen;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

.green-circle {
    position: fixed;
    top: -440px;
    right: -200px;
    z-index: 10;
    -webkit-transform: scale(.25);
        -ms-transform: scale(.25);
            transform: scale(.25);
}
.mic-wrap{
    position: absolute;
    z-index: 999;
    /* border: 1px solid red; */
    width: 113%;
    height: 136px;
    background: #fff;    
    -webkit-box-sizing: content-box;    
            box-sizing: content-box;
    -webkit-box-shadow: 2px 14px 25px #e3e3e3;
            box-shadow: 2px 14px 25px #e3e3e3;
    top: -9px;
}
.mic-wrap h2{
    margin:51px;
}


