
.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 10px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input:focus {
  outline: none;
}

.react-autosuggest__container--open .react-autosuggest__input {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__container--open .react-autosuggest__suggestions-container {
  display: block;
  position: absolute;
  top: 34px;
  width: 100%;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
  max-height: 331px;
  min-height: 38px;
  overflow: hidden;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion:not(:first-child) {
  border-top: 1px solid #ddd;
}

.react-autosuggest__suggestion--focused {
  background-color: #0C7EAF;
  color: #fff;
}

.suggestion-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-repeat: no-repeat;
  position: relative;
}
.priceSpan{
  position: absolute;
  right: 84px;
  top: 2px;
}


.name {
  line-height: 32px;
  font-size: 12px;
  font-family: 'Roboto', sans-serif
}

.highlight {
  color: #666666;
  font-weight: bold;
}
.inCategory strong{color:#FE980F}
.react-autosuggest__suggestion--focused .highlight {
  color: #120000;
}
.input-group{
  width:100%
}
.react-autosuggest__suggestion{
  padding:0 20px;
}
.sugg-cart{
position: absolute;
right: 0px;
display: block;
line-height: 25px;
height: 23px;
top: 5px;
padding: 0 10px;
font-size: 11px;
font-family: 'Roboto';
border-radius: 3px;
cursor: pointer!important;
text-transform: uppercase;
color: #4d4d4d;
background: #fee67c;
background: -webkit-gradient(linear,left top, left bottom,color-stop(0, #fee67c),to(#f2cb76));
background: -webkit-linear-gradient(top,#fee67c 0,#f2cb76);
background: -o-linear-gradient(top,#fee67c 0,#f2cb76);
background: linear-gradient(180deg,#fee67c 0,#f2cb76);
-webkit-filter: progid:dximagetransform.microsoft.gradient(startcolorstr='$btn-yellow-bg-top',endcolorstr='$btn-yellow-bg-bottom',gradienttype=0);
filter: progid:dximagetransform.microsoft.gradient(startcolorstr='$btn-yellow-bg-top',endcolorstr='$btn-yellow-bg-bottom',gradienttype=0);
}
.m-as{
  position: relative;
}
.m-as i.s-search{
  position: absolute;
  right: 0;
  top: 0;
  background:#febd69;
  padding: 10px 13px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.m-as input{
  padding:0 20px;
  border-radius: 3px !important;
}
.clear-s-text{
  position: absolute;
  right: 50px;
  top: 9px;
  color: #8d8d8d;
  z-index: 999;
}
.display-dalalist{
  display: block;
  width:100%;
  background: #fff;
  position: absolute;
  z-index: 99;
  cursor: pointer;  
  border-left:1px solid #eee;
  border-right:1px solid #eee;
}
.display-dalalist .historyitem{
  position: relative;
  padding: 10px 20px;
  border-bottom: 1px solid rgb(239, 239, 239);
}
.display-dalalist span{
  background: #ccc;
  border-radius: 2px;
  position: absolute;
  right: 13px;
  z-index: 999;
  cursor: pointer;
  top: 12px;
}
.historyItemList{
  max-height: 350px;
  overflow: auto;
}
.clearHistory{
  padding: 10px;
  border-bottom: 1px solid #bbb;
  text-align: center;
  color: blue;
}
.suggItemTitle{
  max-width: 471px;
  display: block;  
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}
.m-as input{
  padding: 0 80px 0 20px;
}


