.panel-group .panel-heading + .panel-collapse .panel-body {
  padding-bottom: 0;
  padding-top: 5px;
}
.panel-group .panel-heading + .panel-collapse .panel-body ul li {
}
.panel-group .panel-heading + .panel-collapse .panel-body ul li:last-child {
  padding-bottom: 0;
}
.collapsed .fa-plus:before {
  content: "\f067";
}
.acc-open.collapsed .fa-plus:before {
  content: "\f067";
}
.acc-open .fa-plus:before {
  content: "\f068";
}
.panel-group
  .panel-heading
  + .panel-collapse
  .panel-body
  ul
  li
  span.custom-control-description {
  font-weight: normal;
  font-size: 12px;
  position: relative;
  padding-left: 10px;
  top: 3px;
  font-family: "Roboto";
}
.category-products {
  margin-bottom: 0;
  padding-bottom: 0;
}
.price-range {
  margin-top: 5px;
}
.price-range h2 {
  color: #696763;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
}
.btn-outline-dark {
  color: #343a40;
  background-color: transparent;
  background-image: none;
  border-color: #343a40;
  margin: 5px 0 5px 16px;
}
.badge-warning {
  color: #111;
  background-color: #ffc107;
}
.btn-outline-dark:hover {
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
}
.facet-container {
  padding: 0 20px;
}



