.isa_warning {
    color: #9F6000;
    background-color: #FEEFB3;
    padding:5px 20px;
    position: absolute;
    top: -25px;
    border-radius:5px;
}
.pos-top{
    position: fixed;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 999;
    border-radius: 0;
}
.voice-icon{
    right: -30px;
    position: absolute;
    top: 0;
}
.voice-icon button:active,.voice-icon button:focus{
    color: #9F6000;
    background-color: #FEEFB3;
}


